import React, { useState } from "react";
import "./Projects.css";
function Projects() {
  const url =
    "https://d2slcw3kip6qmk.cloudfront.net/marketing/techblog/angular-directives/5-angular-directives-you-can-use-in-your-project-header@2x.png";
  const [showMenu, setShowMenu] = useState(false);

  let menu;

  if (showMenu) {
    menu = (
      <>
        {/* <div className="wh">samrat</div> */}
        <div className="start_projects">
          <div className="projects_container">
            <div className="projects_thumbnail">
              <h1 className="pro_head">Lorem ipsum dolor sit.</h1>
              <img src={url} alt="" />
            </div>
            <div className="projects_title">
              <a className="page_links" href="#">
                <h1>View</h1>
              </a>
            </div>
          </div>
        </div>
      </>
    );
  }
  const [displayMenu, setDisplayMenu] = useState(false);

  let display;

  if (displayMenu) {
    display = (
      <>
        {/* <div className="wh">samrat</div> */}
        <div className="start_projects">
          <div className="projects_container">
            <div className="projects_thumbnail">
              <h1 className="pro_head">Lorem ipsum dolor sit.</h1>
              <img src={url} alt="" />
            </div>
            <div className="projects_title">
              <a className="page_links" href="#">
                <h1>View</h1>
              </a>
            </div>
          </div>
        </div>
      </>
    );
  }
  const [viewMenu, setViewMenu] = useState(false);

  let view;

  if (viewMenu) {
    view = (
      <>
        {/* <div className="wh">samrat</div> */}
        <div className="start_projects">
          <div className="projects_container">
            <div className="projects_thumbnail">
              <h1 className="pro_head">Lorem ipsum dolor sit.</h1>
              <img src={url} alt="" />
            </div>
            <div className="projects_title">
              <a className="page_links" href="#">
                <h1>View</h1>
              </a>
            </div>
          </div>
        </div>
      </>
    );
  }

  return (
    <div className="projects">
      <div className="icon">
        <div className="projects_btns">
          <button
            className="btns"
            onClick={() => setShowMenu(!showMenu)}
            style={{ fontSize: "px" }}
          >
            <h1 className="projects_header">Basics Projects +</h1>
          </button>
        </div>
        {menu}

        <div className="projects_btns">
          <button
            className="btns"
            onClick={() => setDisplayMenu(!displayMenu)}
            style={{ fontSize: "px" }}
          >
            <h1 className="projects_header">Basics Projects +</h1>
          </button>
        </div>
        {display}
        <div className="projects_btns">
          <button
            className="btns"
            onClick={() => setViewMenu(!viewMenu)}
            style={{ fontSize: "px" }}
          >
            <h1 className="projects_header ">Basics Projects +</h1>
          </button>
        </div>
        {view}
      </div>
    </div>
  );
}
export default Projects;
